import React, { useState, useRef, useEffect } from "react";
import styles from "./ReactMulti-Select-Checkboxes.module.css";
import ariaAnnounce from "./ariaAnnounce";

const ReactMultiSelectCheckboxes = () => {
  const [expanded, setExpanded] = useState(false);
  const [checkCount, setCheckCount] = useState(0);
  const catBreedGroup = useRef(null);
  const totalCheckboxes = 3;

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        catBreedGroup.current &&
        !catBreedGroup.current.contains(event.target)
      ) {
        setExpanded(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const toggleDisclosure = () => {
    setExpanded(!expanded);
  };

  const updateCheckCount = (event) => {
    const newCheckCount = event.target.checked
      ? checkCount + 1
      : checkCount - 1;
    setCheckCount(newCheckCount);
    ariaAnnounce(`${newCheckCount} of ${totalCheckboxes} checkboxes checked`);
  };

  return (
    <div className={styles.container}>
      <button
        type="button"
        aria-expanded={expanded}
        onClick={toggleDisclosure}
        aria-controls="CatBreedGroup"
        className={styles.button}
      >
        <span className={styles.icon}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 80 80"
            focusable="false"
          >
            <path d="M70.3 13.8L40 66.3 9.7 13.8z"></path>
          </svg>
        </span>
        Cat Breeds
        <span className={styles.counter}>
          ({checkCount} of {totalCheckboxes} selected)
        </span>
      </button>
      <div
        role="group"
        aria-labelledby="CatBreedGroupTrigger"
        id="CatBreedGroup"
        ref={catBreedGroup}
        className={expanded ? styles.catBreedGroup : styles.hidden}
      >
        <ul>
          <li>
            <input
              type="checkbox"
              name="catBreed"
              id="catBreed1"
              value="Bengal"
              onChange={updateCheckCount}
            />
            <label htmlFor="catBreed1">Bengal</label>
          </li>
          <li>
            <input
              type="checkbox"
              name="catBreed"
              id="catBreed2"
              value="Siamese"
              onChange={updateCheckCount}
            />
            <label htmlFor="catBreed2">Siamese</label>
          </li>
          <li>
            <input
              type="checkbox"
              name="catBreed"
              id="catBreed3"
              value="Maine Coon"
              onChange={updateCheckCount}
            />
            <label htmlFor="catBreed3">Maine Coon</label>
          </li>
        </ul>
        {/* Add more cat breeds here */}
      </div>
    </div>
  );
};

export default ReactMultiSelectCheckboxes;
